(function () {

///////////////////////////////////////////////////////////////////////////////////////////////////
//                                                                                               //
// packages/mike:mocha-core/server.js                                                            //
//                                                                                               //
///////////////////////////////////////////////////////////////////////////////////////////////////
                                                                                                 //
var Mocha = Npm.require("mocha");                                                                // 1
var Fiber = Npm.require("fibers");                                                               // 2
                                                                                                 // 3
setupGlobals = function(mocha){                                                                  // 4
  //basically a direct copy from meteor/packages/meteor/dynamics_nodejs.js                       // 5
  //except the wrapped function has an argument (mocha distinguishes                             // 6
  //asynchronous tests from synchronous ones by the "length" of the                              // 7
  //function passed into it, before, etc.)                                                       // 8
  var moddedBindEnvironment = function (func, onException, _this) {                              // 9
    Meteor._nodeCodeMustBeInFiber();                                                             // 10
                                                                                                 // 11
    var boundValues = _.clone(Fiber.current._meteor_dynamics || []);                             // 12
                                                                                                 // 13
    if (!onException || typeof(onException) === 'string') {                                      // 14
      var description = onException || "callback of async function";                             // 15
      onException = function (error) {                                                           // 16
        Meteor._debug(                                                                           // 17
          "Exception in " + description + ":",                                                   // 18
          error && error.stack || error                                                          // 19
        );                                                                                       // 20
      };                                                                                         // 21
    }                                                                                            // 22
                                                                                                 // 23
    //IMPORTANT note the callback variable present here, for                                     // 24
    //Metoer.bindEnvironment this is ` return function (/* arguments */) {`                      // 25
    return function (callback) {                                                                 // 26
      var args = _.toArray(arguments);                                                           // 27
                                                                                                 // 28
      var runWithEnvironment = function () {                                                     // 29
        var savedValues = Fiber.current._meteor_dynamics;                                        // 30
        try {                                                                                    // 31
          // Need to clone boundValues in case two fibers invoke this                            // 32
          // function at the same time                                                           // 33
          Fiber.current._meteor_dynamics = _.clone(boundValues);                                 // 34
          var ret = func.apply(_this, args);                                                     // 35
        } catch (e) {                                                                            // 36
          onException(e);                                                                        // 37
        } finally {                                                                              // 38
          Fiber.current._meteor_dynamics = savedValues;                                          // 39
        }                                                                                        // 40
        return ret;                                                                              // 41
      };                                                                                         // 42
                                                                                                 // 43
      if (Fiber.current)                                                                         // 44
        return runWithEnvironment();                                                             // 45
      Fiber(runWithEnvironment).run();                                                           // 46
    };                                                                                           // 47
  };                                                                                             // 48
                                                                                                 // 49
                                                                                                 // 50
  var mochaExports = {};                                                                         // 51
  mocha.suite.emit("pre-require", mochaExports, undefined, mocha);                               // 52
  //console.log(mochaExports);                                                                   // 53
                                                                                                 // 54
  // 1. patch up describe function so it plays nice w/ fibers                                    // 55
  // 2. trick to allow binding the suite instance as `this` value                                // 56
  // inside of describe blocks, to allow e.g. to set custom timeouts.                            // 57
  function wrapRunnable(func) {                                                                  // 58
    return function() {                                                                          // 59
      // `this` will be bound to the suite instance, as of Mocha's `describe` implementation     // 60
      Meteor.bindEnvironment(func.bind(this), function(err) { throw err; })();                   // 61
    }                                                                                            // 62
  }                                                                                              // 63
                                                                                                 // 64
  global.describe = function (name, func){                                                       // 65
    return mochaExports.describe(name, wrapRunnable(func));                                      // 66
  };                                                                                             // 67
  global.describe.skip = mochaExports.describe.skip;                                             // 68
  global.describe.only = function(name, func) {                                                  // 69
    mochaExports.describe.only(name, Meteor.bindEnvironment(func, function(err){ throw err; })); // 70
  };                                                                                             // 71
                                                                                                 // 72
  //In Meteor, these blocks will all be invoking Meteor code and must                            // 73
  //run within a fiber. We must therefore wrap each with something like                          // 74
  //bindEnvironment. The function passed off to mocha must have length                           // 75
  //greater than zero if we want mocha to run it asynchronously. That's                          // 76
  //why it uses the moddedBindEnivronment function described above instead                       // 77
                                                                                                 // 78
  //We're actually having mocha run all tests asynchronously. This                               // 79
  //is because mocha cannot tell when a synchronous fiber test has                               // 80
  //finished, because the test runner runs outside a fiber.                                      // 81
                                                                                                 // 82
  //It is possible that the mocha test runner could be run from within a                         // 83
  //fiber, but it was unclear to me how that could be done without                               // 84
  //forking mocha itself.                                                                        // 85
                                                                                                 // 86
  global['it'] = function (name, func){                                                          // 87
    wrappedFunc = function(callback){                                                            // 88
      if (func.length == 0){                                                                     // 89
        func();                                                                                  // 90
        callback();                                                                              // 91
      }                                                                                          // 92
      else {                                                                                     // 93
        func(callback);                                                                          // 94
      }                                                                                          // 95
    }                                                                                            // 96
                                                                                                 // 97
    boundWrappedFunction = moddedBindEnvironment(wrappedFunc, function(err){                     // 98
      throw err;                                                                                 // 99
    });                                                                                          // 100
                                                                                                 // 101
    mochaExports['it'](name, boundWrappedFunction);                                              // 102
  };                                                                                             // 103
  global.it.skip = mochaExports.it.skip;                                                         // 104
  global.it.only = function(name, func) {                                                        // 105
    mochaExports.it.only(name, Meteor.bindEnvironment(func, function(err){ throw err; }));       // 106
  };                                                                                             // 107
                                                                                                 // 108
  ["before", "beforeEach", "after", "afterEach"].forEach(function(testFunctionName){             // 109
    global[testFunctionName] = function (func){                                                  // 110
      wrappedFunc = function(callback){                                                          // 111
        if (func.length == 0){                                                                   // 112
          func();                                                                                // 113
          callback();                                                                            // 114
        }                                                                                        // 115
        else {                                                                                   // 116
          func(callback);                                                                        // 117
        }                                                                                        // 118
      }                                                                                          // 119
                                                                                                 // 120
      boundWrappedFunction = moddedBindEnvironment(wrappedFunc, function(err){                   // 121
        throw err;                                                                               // 122
      });                                                                                        // 123
      mochaExports[testFunctionName](boundWrappedFunction);                                      // 124
    }                                                                                            // 125
  });                                                                                            // 126
}                                                                                                // 127
                                                                                                 // 128
///////////////////////////////////////////////////////////////////////////////////////////////////

}).call(this);
